body {
    background-color: #2b2b2b;
}

.home-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    color: white;
    text-align: center;
    position: relative;
    color: white;
    text-align: center; 
}

.home-container h1 {
    color: aliceblue;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-shadow: 1px 1px 2px rgba(212, 212, 212, 0.7);
    font-size: 8vh;
    margin-top: 20px;
    opacity: 0;
    transform: translateY(-20px);
    animation: fadeIn 1s forwards;
    animation-delay: 0.5s;
}
      
    @keyframes fadeIn {
        to {
          opacity: 1;
          transform: translateY(0);
        }
    }
      



.home-container p {
    font-size: 3vh;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: bisque;
    opacity: 0;
    transform: translateX(-20px);
    animation: fadeIn 1s forwards;
    animation-delay: 0.5s;
}
      
    @keyframes fadeIn {
        to {
          opacity: 1;
          transform: translateX(0);
        }
    }

  
.image-container {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
.image-container img {
    width: 80vh;
    height: auto; 
    max-width: 100%;
}


  
  