.gallery-title {
    position: absolute;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    top: 3px; 
    left: 50%;
    transform: translateX(-50%);
    color: wheat;
    font-size: 2em;
    text-align: center;
    z-index: 1;
}

.gallery-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    padding: 20px;
  }
  
  .art-item {
    position: relative;
    overflow: hidden;
  }
  
  .art-item img,
  .art-item video {
    width: 100%;
    height: auto; 
    border-radius: 8px;
  }
  
  .art-item video {
    border: none;
  }
  