.contact-container {
    position: relative;
    min-height: 100vh;
    color: wheat;
    text-align: center;
    padding: 20px;
    border-radius: 8px;
  }

  .contact-container h1 {
    font-size: 6vh;
    text-shadow: 1px 1px 2px rgba(212, 212, 212, 0.7);
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  }

.contact-container p {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }

.contact-container label {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 4vh;
    margin-top: 25px;
}
  
  .background-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; 
    background-image: url('https://example.com/path/to/your/contact-background.gif');
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
  
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  input, textarea {
    font-family: "SF Pro";
    font-size: 12px;
    max-width: 120vh;
    padding: 0.875rem;
    font-size: 1rem;
    border: 1.5px solid #414141;
    border-radius: 0.5rem;
    box-shadow: 2.5px 3px 0 #000;
    outline: none;
    transition: ease 0.25s;
  }

input:hover {
    box-shadow: 5.5px 7px 0 rgb(187, 147, 110);
  }

input:focus {
    box-shadow: 5.5px 7px 0 black;
  }
  

button {
    cursor: pointer;
    position: relative;
    margin-top: 45px;
    width: 23vh;
    padding: 10px 24px;
    font-size: 18px;
    color: rgb(193, 163, 98);
    border: 2px solid rgb(193, 163, 98);
    border-radius: 34px;
    background-color: transparent;
    font-weight: 600;
    transition: all 0.3s cubic-bezier(0.23, 1, 0.320, 1);
    overflow: hidden;
  }
  
button::before {
    content: '';
    position: absolute;
    inset: 0;
    margin: auto;
    width: 50px;
    height: 50px;
    border-radius: inherit;
    scale: 0;
    z-index: -1;
    background-color: rgb(193, 163, 98);
    transition: all 0.6s cubic-bezier(0.23, 1, 0.320, 1);
  }
  
button:hover::before {
    scale: 3;
  }
  
button:hover {
    color: #212121;
    scale: 1.1;
    box-shadow: 0 0px 20px rgba(193, 163, 98,0.4);
  }
  
button:active {
    scale: 1;
  }
  
  
  
  