* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.container {
    margin: 1vh;
    text-align: center;
}

.site-title {
    font-size: 3rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-shadow: 
    0 0 5px rgba(255, 255, 255, 0.8),
    0 0 15px rgb(241, 122, 86);
}

.nav {
    background-color: #3d3d3d;
    color: wheat;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 18px;
    color: #e1e1e1;
    font-family: inherit;
    font-weight: 800;
    cursor: pointer;
    position: relative;
    border: none;
    background: none;
    text-transform: uppercase;
    transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
    transition-duration: 400ms;
    transition-property: color;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li:focus, .nav li:hover {
    color: #fff;
    text-shadow: 
    0 0 5px rgba(255, 255, 255, 0.8),
    0 0 15px rgb(245, 205, 97);
}

.nav li:focus:after, button:hover:after {
    width: 100%;
    left: 0%;
}

.nav li:after{
    content: "";
  pointer-events: none;
  bottom: -2px;
  left: 50%;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #fff;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: width, left;
}
